import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/services/http.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-main-content',
  templateUrl: './main-content.component.html',
  styleUrls: ['./main-content.component.scss']
})
export class MainContentComponent implements OnInit {
  data : any =[];
  item : any =[];
  count : number = 0;
  showAccordion:boolean = false;
  pageNav:boolean = false;
  constructor(private httpService:HttpService,private route: ActivatedRoute) { }

  ngOnInit() {
    this.getAllowanceFormDetail();
    this.getMainContentDetail();
    this.checkPath();
  }
  checkPath(){
    let pageUrl = this.route.snapshot.url[0].path;
    if(pageUrl === "forms" || pageUrl === "search_and_seizures" ){
      this.showAccordion = true;
    }
    else if(pageUrl === "case_laws" || pageUrl === "training"|| pageUrl === "books_and_manual"){
      this.showAccordion = false;
    }
    this.pageNavCheck(pageUrl);
  }
  pageNavCheck(pageUrl){
    console.log(pageUrl,"ok");
    if(pageUrl === "case_laws" || pageUrl === "books_and_manual"){
      this.pageNav = true;
    }
    else if(pageUrl === "forms" || pageUrl === "search_and_seizures"|| pageUrl === "training"){
      this.pageNav = false;
    }
  }
  getAllowanceFormDetail(){
    this.httpService.getAllowanceForm().subscribe((data)=>{
      
      this.data = data;
      for(var i=0; i< this.data.length; i++){
        var obj = data[i];
        this.count += obj["details"].length;
      }  
    });
  }
  getMainContentDetail(){
    this.httpService.getMainContent().subscribe(item=>{
      this.item=item;
    })
  };
}
