import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/services/http.service';

@Component({
  selector: 'app-notice-board-slide',
  templateUrl: './notice-board-slide.component.html',
  styleUrls: ['./notice-board-slide.component.scss']
})

export class NoticeBoardSlideComponent implements OnInit {
  data: any = [];
  newTag: boolean;
  constructor(private httpservice: HttpService) { }

  ngOnInit() {
    this.getNoticeBoardSlideDetail();
  }
  getNoticeBoardSlideDetail(){
    this.httpservice.getNoticeBoardSlide().subscribe((data)=>{
      this.data = data;

    });

  }

}
