import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/services/http.service';
import { ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-nav-cards-details',
  templateUrl: './nav-cards-details.component.html',
  styleUrls: ['./nav-cards-details.component.scss']
})
export class NavCardsDetailsComponent implements OnInit {
  constructor(private httpService: HttpService, private route: ActivatedRoute) { }
  data: any = [];
  width100 = false;
  ngOnInit() {
    this.routePage();
  }
  routePage() {
    const pageUrl = this.route.snapshot.url[0].path;
    if(pageUrl == "case_laws"){
      this.width100 = true;
    }
    else{
      this.width100 = false;
    }
    this.httpService.routePage(pageUrl).subscribe((data) => {
    });
  }
}
