import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/services/http.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-blogs-view',
  templateUrl: './blogs-view.component.html',
  styleUrls: ['./blogs-view.component.scss']
})
export class BlogsViewComponent implements OnInit {
  data: any = [];
  currentPage :any;
  constructor(private httpService:HttpService, private router:Router) { }

  ngOnInit() {
    this.getJson();
  }


  getJson(){
    this.httpService.getHomeBlog().subscribe((data) => {
      console.log(data);
      this.data=data;
    });
  }
  onBlogCard(element){
    var url = element.title.replace(/[^A-Z0-9]/ig, "_");
    console.log(element,url,"jjjj");

    this.router.navigate(['/blogs',url]);
  }
}
