import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/services/http.service';


@Component({
  selector: 'app-notice-board-view',
  templateUrl: './notice-board-view.component.html',
  styleUrls: ['./notice-board-view.component.scss']
})
export class NoticeBoardViewComponent implements OnInit {

  data: any = [];
  count = 0;


  constructor(private httpservice: HttpService) { }

  ngOnInit() {
    this.getNoticeBoardViewDetail();
  }
  getNoticeBoardViewDetail() {
    this.httpservice.getNoticeBoardView().subscribe((data) => {

      this.data = data;

      for (let i = 0; i < this.data.length; i++) {

        this.count = this.data.length;

      }



    });
  }

}
