import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-nav-cards',
  templateUrl: './nav-cards.component.html',
  styleUrls: ['./nav-cards.component.scss']
})
export class NavCardsComponent implements OnInit {
  data: any = [];
  constructor(private httpservice: HttpService, private route: Router) { }

  ngOnInit() {
    this.getJson();
  }

  getJson() {
    this.httpservice.getNavCard().subscribe((data) => {
      this.data = data;
    });
  }

  routeUrl(card) {
    this.route.navigate([card.url]);
  }
}
