import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/services/http.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-side-nav',
  templateUrl: './side-nav.component.html',
  styleUrls: ['./side-nav.component.scss']
})
export class SideNavComponent implements OnInit {
  constructor(private httpService: HttpService, private route: ActivatedRoute) { }
  data: any =[] ;
  finalData = [];
  pageUrl='';
  imgSrc = false;
  public isClicked = [];
  activefirst:boolean = true;
  selectedItem:any;
  ngOnInit() {
    this.pageUrl = this.route.snapshot.url[0].path;
    console.log(this.pageUrl);
    this.SideNavCurrentPage();
  }
  //to call json for side nav
  SideNavCurrentPage() {
    this.httpService.getSideNavCurrentPage().subscribe((data) => {
      this.data = data;
      console.log(this.data,"side nav content");
       this.finalData=this.data.filter((item) =>item.page===this.pageUrl);
       console.log(this.finalData);
    this.selectedItem = this.finalData[0].list[0];

    });
  }
  listClick(event, newValue) {
    if(event.target.classList.contains('active')){
      alert("Asd");
    this.imgSrc = true;
    }
    this.selectedItem = newValue;  
    console.log(event.target.classList.contains('active'),"new value");

    this.activefirst = false;
   
}
}
