import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-view-all',
  templateUrl: './view-all.component.html',
  styleUrls: ['./view-all.component.scss']
})
export class ViewAllComponent implements OnInit {
  currentPage = '';

  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    const pageUrl = this.route.snapshot.url;
    this.routePage(pageUrl[0].path);

  }
  routePage(pageUrl): any {

    switch (pageUrl) {
      case 'blogs': this.currentPage = 'blogs';
        break;
      case 'employee_forum': return this.currentPage = 'employee_forum';
        break;
      case 'notice_board': this.currentPage = 'notice_board';
        break;
      default: return;
    }
  }
}
