import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpService } from 'src/app/services/http.service';

@Component({
  selector: 'app-blog-inner',
  templateUrl: './blog-inner.component.html',
  styleUrls: ['./blog-inner.component.scss']
})
export class BlogInnerComponent implements OnInit {
  bannerText : string ="";
  data: any=[];
  result :  string ="";

  constructor(private httpService:HttpService,private router: Router) { }

  ngOnInit() {
    this.bannerText= this.router.url;
    this.result = this.router.url.substring(this.bannerText.lastIndexOf("/")).replace('/'," ").split("_").join(" ");
    console.log(this.result);
    this.getJson();
  }
  getJson(){
    this.httpService.getHomeBlog().subscribe((data) => {
      console.log(data);
      this.data=data;
    });
  }
}
