import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/services/http.service';

@Component({
  selector: 'app-employee-forum',
  templateUrl: './employee-forum.component.html',
  styleUrls: ['./employee-forum.component.scss']
})
export class EmployeeForumComponent implements OnInit {
  data: any = [];
  newTag: boolean;
  constructor(private httpservice: HttpService) { }

  ngOnInit() {
    this.getEmployeeForumDetail();
  }
  getEmployeeForumDetail(){
    this.httpservice.getEmployeeForum().subscribe((data)=>{
      this.data=data;
    });

  }

}
