import { NgModule } from '@angular/core';
import { Routes, RouterModule, RouteConfigLoadEnd } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { NavCardsDetailsComponent } from './nav-cards-details/nav-cards-details.component';
import { ViewAllComponent } from './view-all/view-all.component';
import { BlogInnerComponent } from './view-all/blog-inner/blog-inner.component';

const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'forms', component: NavCardsDetailsComponent },
  { path: 'search_and_seizures', component: NavCardsDetailsComponent },
  { path: 'case_laws', component: NavCardsDetailsComponent },
  { path: 'training', component: NavCardsDetailsComponent },
  { path: 'books_and_manual', component: NavCardsDetailsComponent },
  { path: 'faqs', component: NavCardsDetailsComponent },
  { path: 'blogs', component: ViewAllComponent },
  { path: 'blogs/:id', component: BlogInnerComponent },
  { path: 'employee_forum', component: ViewAllComponent },
  { path: 'notice_board', component: ViewAllComponent },

  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: '**', component: HomeComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
